import { Component } from "./base/Component";
import { ensureElement } from "../utils/utils";
export class Order extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.formId = 'order';
        this._email = ensureElement('input[name="email"]', container);
        this._phone = ensureElement('input[name="phone"]', container);
        this._button = ensureElement('button[type="submit"]', container);
        this._errors = ensureElement('.form__errors', container);
        this.container.addEventListener('input', (e) => {
            const target = e.target;
            const field = target.name;
            const value = target.value;
            this.onInputChange(field, value);
        });
        this.container.addEventListener('submit', (e) => {
            e.preventDefault();
            this.events.emit('order:submit');
        });
    }
    onInputChange(field, value) {
        this.events.emit(`${this.formId}.${String(field)}:change`, {
            field,
            value
        });
    }
    set valid(value) {
        this._button.disabled = !value;
    }
    set errors(value) {
        this.setText(this._errors, value);
    }
    render(state) {
        const { email, phone } = state;
        this._email.value = email || '';
        this._phone.value = phone || '';
        return super.render(state);
    }
}
