import { Component } from "./base/Component";
import { ensureElement } from "../utils/utils";
import { LotCard } from "./LotCard";
export class Basket extends Component {
    constructor(container) {
        super(container);
        this._list = ensureElement('.basket__list', container);
        this._total = ensureElement('.basket__total', container);
        this._button = ensureElement('.basket__action', container);
    }
    render(data) {
        this._list.replaceChildren(...data.items.map(item => new LotCard(ensureElement('.bid')).render(item)));
        this.setText(this._total, `Сумма: ${data.total} ₽`);
        return super.render(data);
    }
    set buttonState(state) {
        this._button.disabled = state;
    }
}
