import './scss/styles.scss'; // Подключаем стили
import { EventEmitter } from "./components/base/events";
import { AuctionAPI } from "./components/AuctionAPI";
import { API_URL, CDN_URL } from "./utils/constants";
import { AppData } from "./components/AppData";
import { Order } from "./components/Order";
import { Basket } from "./components/Basket";
import { Modal } from "./components/common/Modal";
import { ensureElement } from "./utils/utils";
// Инициализация
const events = new EventEmitter();
const api = new AuctionAPI(CDN_URL, API_URL);
const appData = new AppData({}, events);
// Глобальные контейнеры
const modalContainer = ensureElement('#modal-container');
const basketContainer = ensureElement('.basket');
const orderForm = ensureElement('.form');
// Компоненты
const modal = new Modal(modalContainer, events);
const basket = new Basket(basketContainer);
const order = new Order(orderForm, events);
// Загрузка данных с сервера
api.getLotList()
    .then((lots) => {
    appData.setLots(lots);
})
    .catch((err) => {
    console.error('Ошибка при загрузке лотов:', err);
});
// Подписка на события
events.on('basket:changed', () => {
    basket.render({
        items: appData.getBasket,
        total: appData.getBasket.reduce((sum, item) => sum + item.price, 0)
    });
});
events.on('order:submit', () => {
    if (appData.validateOrder()) {
        api.orderLots(appData.getOrder)
            .then(() => {
            appData.clearBasket();
            // Обновляем содержимое элемента .state
            const stateElement = ensureElement('.state');
            stateElement.innerHTML = `
                    <h2>Покупка успешно оформлена</h2>
                    <p>Ожидайте доставки</p>
                `;
            // Передаём элемент в модальное окно
            modal.render({
                content: stateElement
            });
        })
            .catch((err) => {
            console.error('Ошибка при оформлении заказа:', err);
            order.errors = 'Ошибка оформления заказа';
        });
    }
    else {
        order.errors = 'Заполните все поля';
    }
});
