import { Model } from "./base/Model";
export class AppData extends Model {
    constructor(data, events) {
        super(data, events);
        this.events = events;
        this._lots = [];
        this._basket = [];
        this._order = {
            email: '',
            phone: '',
            items: []
        };
        this._preview = null;
        if (data.lots)
            this._lots = data.lots;
        if (data.basket)
            this._basket = data.basket;
        if (data.order)
            this._order = data.order;
        if (data.preview)
            this._preview = data.preview;
    }
    setLots(lots) {
        this._lots = lots;
        this.emitChanges('lots:changed');
    }
    setPreview(lot) {
        this._preview = lot;
        this.emitChanges('preview:changed');
    }
    addToBasket(lot) {
        this._basket.push(lot);
        this.emitChanges('basket:changed');
    }
    removeFromBasket(lot) {
        this._basket = this._basket.filter(el => { el.id !== lot.id; });
        this.emitChanges('basket:changed');
    }
    clearBasket() {
        this._basket = [];
        this.emitChanges('basket:changed');
    }
    setOrderField(field, value) {
        this._order[field] = value;
        this.emitChanges('order:changed');
    }
    get getTotal() {
        return this._basket.reduce((total, item) => total + item.price, 0);
    }
    get getBasket() {
        return this._basket;
    }
    get getLots() {
        return this._lots;
    }
    get getPreview() {
        return this._preview;
    }
    get getOrder() {
        return this._order;
    }
    validateOrder() {
        const errors = {};
        if (!this._order.email)
            errors.email = 'Укажите email';
        if (!this._order.phone)
            errors.phone = 'Укажите телефон';
        this.emitChanges('formErrors:changed', errors);
        return Object.keys(errors).length === 0;
    }
}
